<template>
    <div class="info-div staffMana-info" ref="trys" >
        <div class="info-header" style="left: 1.1em">
            <span class="callback" @click="resetComponent">充电用户管理 </span>
            <span> > 用户详情</span>
        </div>
        <div class="info-container" style="margin-top: 1em;height: calc(100% - 1em);">
            <div class="list-content list-contentB detail-content" style="height:calc(35% - 3em);;padding:0">
                <div class="content-left">
                    <p class="HF-p">
                        <span>当前账户余额</span>
                        <i class="hasIcon hasImg" @click="getDetail" style="margin-right:0">
                            <img src="dist/img/costControl/fresh.png" style="height:auto" />
                        </i>
                    </p>
                    <p class="content-p" style="margin-bottom:1em">
                        <span>{{this.detailData.remainAmount || '--'}}</span>元
                    </p>
                    <p>使用次数：<span style="color: #C4895F">{{this.detailData.chargeCount||'--'}}</span></p>
                </div>

                <div class="content-right">
                    <ul class="form-list" style="padding:1em">
                        <li v-for="(item,$index) in commInfo" style="margin:0!important">
                            <span class="label-span">{{item.text}}:</span>
                            <span class="label-span" style="max-width: calc(100% - 8em);" :title="item.value"> &nbsp;&nbsp;&nbsp;{{item.value}}</span>
                        </li>
                    </ul>
                </div>
                <div class="content-bottom">
                    状态:
                    <button class="list-operate" @click="blacklist" :class="{'normal':normal, 'defriend':!normal}" v-show="Finish"></button>
                </div>
            </div>
            <div class="list-content list-contentB" style="font-size: 0.94em;height: calc(65% - 1em);padding:1em;">
                <div class="tabs-show tab-div" style="margin:0 2em 1em 0em !important">
                    <ul>
                        <li v-for="(tabs, $index) in btnOpt" :class="{'selectedLi':  $index == index}"  style="min-width: 6em; font-size:0.94em;padding: 0.72em 1em"
                            @click="getTab(tabs, $index)">
                            {{tabs.name}}
                        </li>
                    </ul>
                </div>
                <div class="operate-div" style="margin-right: 0.2em">
                    <template v-for="(item, index) in formList">
                        <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom" :key="index"></inputs>
                        <datePicker ref="timer" style="margin-left:10px" v-if="item.type == 'timer'" type= "doubleDate"  :dateOpt="item.options"
                                    @getDatePicker="getFrom"></datePicker>
                        <selects ref="selects"  v-if="item.type == 'select'" :option="item.options" @getSelect="getFrom"></selects>
                    </template>
                </div>
                <div ref="myOwnTable" class="table-container" style="height:calc(100% - 3em)">
                    <tables ref="myTable"  class="dialogTable" v-if="tableShow" :tableOpt="tableOption"
                            @rowDbClick="getDetails" style="margin:0em;width:100%;"></tables>
                </div>
            </div>
        </div>
        <dialogs type="alert" ref="alerts"></dialogs>
        <dialogs type="customer" ref="detail" width="65em" maxHeight="620px">
            <div slot="header">
                <span>{{title}}</span>
            </div>
            <div slot="content">
                <div class="staffMana-content" v-if="isDialogTable">
                    <div class="mychar-box">
                        <div ref="currentChart">
                            <div  class="emptyShowPic">
                                <div class="emptyContent">
                                    <div v-if="isLoading">
                                        <img src="dist/img/empty-data.png" >
                                        <p>查不到相关数据</p>
                                    </div>
                                    <img src="dist/img/loading.gif" v-if="!isLoading" style="width: 5em;height: 5em">
                                </div>
                            </div>
                        </div>
                        <div ref="voltageChart">
                            <div  class="emptyShowPic">
                                <div class="emptyContent">
                                    <div v-if="isLoading">
                                        <img src="dist/img/empty-data.png" >
                                        <p>查不到相关数据</p>
                                    </div>
                                    <img src="dist/img/loading.gif" v-if="!isLoading" style="width: 5em;height: 5em">
                                </div>
                            </div>
                        </div>
                        <div ref="powerChart">
                            <div  class="emptyShowPic">
                                <div class="emptyContent">
                                    <div v-if="isLoading">
                                        <img src="dist/img/empty-data.png" >
                                        <p>查不到相关数据</p>
                                    </div>
                                    <img src="dist/img/loading.gif" v-if="!isLoading" style="width: 5em;height: 5em">
                                </div>
                            </div>
                        </div>
                    </div>
                    <tables ref="caution"  class="dialogTable" :tableOpt="dialogTable"></tables>
                </div>
            </div>
        </dialogs>
    </div>
</template>
<script>
    export default{
        data() {
            return {
                tableShow: false, //table延迟加载
                index: 0, //选中的设备下标
                normal: '',
                Finish: false,
                detailData: '',
                tableOption: {
                    rowName: 'data',
                    column: [],
                    pageSize: 8,
                    pageNumber: 1,
                    method: 'post',
                    queryParam: {},
                    isExport: true,
                    url: "charger/useRecordList",
                    exportURL: 'charger/useRecordListDownload'
                },
                useParam: {
                    "customerGuid": "",
                    "startDate": "",
                    "endDate": "",
                    "deviceNo": ""
                },
                chargeParam: {
                    "customerGuid": "",
                    "startDate": "",
                    "endDate": "",
                    "payMethod": 0,
                    "payState": 2
                },
                withdrawCashParam: {
                    "customerGuid": "",
                    "startDate": "",
                    "endDate": "",
                    "withdrawMethod": 0,
                    "status": 1
                },
                searchArr: ['useParam', 'chargeParam', 'withdrawCashParam'],
                btnOpt: [
                    {name: '使用记录', status: 0, url: "charger/useRecordList", exportURL: 'charger/useRecordListDownload'},
                    {name: '充值记录', status: 1, url: "charger/chargeRecordList", exportURL: 'charger/chargeRecordListDownload'},
                    {name: '提现记录', status: 2, url: "charger/withdrawCashList", exportURL: 'charger/withdrawCashListDownload'}
                ],
                // 以下为弹框
                lineData: {},
                lineV: '',
                lineA: '',
                lineR: '',
                isDialogTable: false,
                title: '',
                isLoading: false,
                dialogTable:{
                    column: [
                        {title: '时间', width: '140', field: 'heartTime', textAlign: 'center'},
                        // {title: '端口', width: '120', field: 'port', textAlign: 'center'},
                        {title: '状态', field: 'state', textAlign: 'center',  width: 120,type: 'string',tips: 'eventContent',
                            formatter: function (val, index, row, obj) {
                                let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                                switch(vals) {
                                    case '0':
                                        return `<div class="green">正常</div>`
                                    case '1':
                                        //${row.eventContent}
                                        return `<div class="red">告警</div>`
                                    case '-1':
                                        return `<div class="orange">充电结束</div>`
                                    default:
                                        return `<div>--</div>`
                                }

                            }
                        },
                        {title: '电压', width: '120', field: 'voltage', textAlign: 'center'},
                        {title: '电流', width: '120', field: 'current', textAlign: 'center'},
                        {title: '功率', width: '120', field: 'power', textAlign: 'center'},
                        {title: '电量', width: '120', field: 'activeTotal', textAlign: 'center'},
                        {title: '剩余时间(分钟)', width: '160', field: 'remainTime', textAlign: 'center',type: 'string',formatter: function (val, index, row, obj) {
                                if(typeof val !== 'undefined') {
                                    return val.toString()
                                }
                                return '--'
                            }
                        }
                    ],
                    method: 'post',
                    url: 'charger/queryHeartBychargingGuid',
                    // noFooter: true,
                    queryParam: {},
                    pageSize: 20,
                    pageNumber: 1,
                    height: 250,
                    isExport: true,
                    exportURL: 'charger/downHeartBychargingGuid',
                }
            }
        },
        props: ['row', 'controls'],
        computed: {
            columns: function () {
                return [
                    [
                        {title: '序号', field: 'Index', textAlign: 'center', width: '40'},
                        {title: '时间', field: 'date', textAlign: 'center', width: '140'},
                        {title: '停充方式', field: 'deviceState', textAlign: 'center', width: '100',type: 'string',
                            formatter: function (val, index, row, obj) {
                                let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                                switch(vals) {
                                    case '自动停止':
                                        return `<div style="color: #468CD6">自动停止</div>`
                                    case '手动停止':
                                        return `<div style="color: #F49E0F">手动停止</div>`;
                                    case '异常停止':
                                        return `<div style="color: #E24E4E">异常停止</div>`;
                                    default:
                                        return '--'
                                }

                            }},
                        {title: '方案', field: 'payCategory', textAlign: 'center', width: '100'},
                        {title: '支付方式', field: 'payMethod', textAlign: 'center',  width: '100'},
                        {title: '设备编号', field: 'deviceNo', textAlign: 'center', width: '100'},
                        {title: '端口号', field: 'port', textAlign: 'center', width: '80'},
                        {title: '电量(KW·h)', field: 'deviceElect', textAlign: 'center',  width: '100'},
                        {title: '充电时长(分钟)', field: 'useTime', textAlign: 'center',  width: '100'},
                        {title: '开始时间', field: 'startDate', textAlign: 'center',  width: '140'},
                        {title: '结束时间', field: 'endDate', textAlign: 'center',  width: '140'},
                        {title: '地址', field: 'installAddress', textAlign: 'center',  width: '150'},
                        {title: '电价(元)', field: 'price', textAlign: 'center',  width: '50'},
                    ],
                    [
                        {title: '序号', field: 'Index', textAlign: 'center',  width: '40', type: 'string'},
                        {title: '充值状态', field: 'payState', textAlign: 'center',  width: '80', type: 'string'},
                        {title: '充值时间', field: 'date', textAlign: 'center', width: '140'},
                        {title: '充值方式', field: 'payMethod', textAlign: 'center', width: '100'},
                        {title: '充值金额(元)', field: 'payMoney', textAlign: 'center', width: '100'},
                        {title: '账户余额(元)', field: 'remainAmount', textAlign: 'center', width: '100'},
                        {title: '人员ID', field: 'userId', textAlign: 'center', width: '150'}
                    ],
                    [
                        {title: '序号', field: 'Index', textAlign: 'center', width: '40'},
                        {title: '提现状态', field: 'status', textAlign: 'center', width: '80'},
                        {title: '提现时间', field: 'date', textAlign: 'center', width: '140'},
                        {title: '提现方式', field: 'withdrawMethod', textAlign: 'center', width: '100',},
                        {title: '提现金额(元)', field: 'withdrawMoney', textAlign: 'center', width: '100'},
                        {title: '账户余额(元)', field: 'remainAmount', textAlign: 'center', width: '100',},
                        {title: '人员ID', field: 'userId', textAlign: 'center', width: '150'}
                    ]
                ]
            },
            commInfo(){
                return [
                    {text: '手机号', value: this.detailData.customerContact || '--'},
                    {text: '微信', value: this.detailData.webcharNo || '未绑定'},
                    {text: '支付宝', value: this.detailData.alipayUserId || '未绑定'},
                    {text: 'IC卡数量', value: this.detailData.cardId || '未绑定'},
                    {text: '月卡剩余次数', value: this.detailData.remainCnt || '--'},
                    {text: '月卡有效期', value: this.detailData.expireTime || '--'},
                ]
            },
            formList: function () {
                let self = this;
                let setText = function (value) {
                    switch (value){
                        case 0:
                            return '全部';
                        case 1:
                            return '微信';
                        case 2:
                            return '支付宝';
                        case 3:
                            return '现金';
                    }
                };
                let ret = [
                    [{
                        type: 'timer',
                        options: {
                            key: 'searchDate.useParam',
                            value: {startDate: self.useParam.startDate, endDate: self.useParam.endDate},
                            width: '23em',
                            fontSize: '0.8em'
                        }
                    },{
                            type: 'input',
                            options: {
                                type: 'typeSearch',
                                key: 'useParam',
                                value: self.useParam.deviceNo,
                                selectOpt: {
                                    width: '7em',
                                    cs: 'client',
                                    data: [
                                        {text: '设备编号', value: 'deviceNo'},
                                    ]
                                }
                            }
                        }
                    ],
                    [
                        {
                            type: 'select',
                            options: {
                                width: '7em',
                                cs: 'client',
                                constant: {value: self.chargeParam.payState, text: setText(self.chargeParam.payState)},
                                data: [
                                    {text: '全部', value: 2},
                                    {text: '待支付', value: 0},
                                    {text: '支付成功', value: 1},
                                ],
                                key: 'chargeParam.payState'
                            }
                        },{
                            type: 'select',
                            options: {
                                width: '7em',
                                cs: 'client',
                                constant: {value: self.chargeParam.payMethod, text: setText(self.chargeParam.payMethod)},
                                data: [
                                    {text: '充值方式', value: 0},
                                    {text: '微信', value: 1},
                                    {text: '支付宝', value: 2},
                                    {text: '现金', value: 3},
                                ],
                                key: 'chargeParam.payMethod'
                            }
                        },{
                            type: 'timer',
                            options: {
                                key: 'searchDate.chargeParam',
                                value: {startDate: self.chargeParam.startDate, endDate: self.chargeParam.endDate},
                                width: '23em',
                                fontSize: '0.8em'
                            }
                        }
                    ],
                    [
                        {
                            type: 'select',
                            options: {
                                width: '8em',
                                cs: 'client',
                                constant: {value: self.withdrawCashParam.status, text:setText(self.withdrawCashParam.status)},
                                data: [
                                    {text: '提现成功', value: 1},
                                    {text: '提现处理中', value: 0},
                                    {text: '提现失败', value: 2},
                                ],
                                key: 'withdrawCashParam.status'
                            }
                        },{
                            type: 'select',
                            options: {
                                width: '7em',
                                cs: 'client',
                                constant: {value: self.withdrawCashParam.withdrawMethod, text:setText(self.withdrawCashParam.withdrawMethod)},
                                data: [
                                    {text: '提现方式', value: 0},
                                    {text: '微信', value: 1},
                                    {text: '支付宝', value: 2},
                                ],
                                key: 'withdrawCashParam.withdrawMethod'
                            }
                        },{
                            type: 'timer',
                            options: {
                                key: 'searchDate.withdrawCashParam',
                                value: {startDate: self.withdrawCashParam.startDate, endDate: self.withdrawCashParam.endDate},
                                width: '23em',
                                fontSize: '0.8em'
                            }
                        }
                    ]
                ];
                return ret[this.index];
            },
        },
        methods: {
            blacklist: function () {
                let self = this;
                this.getRequest({
                    url: 'charger/setCustomerState',
                    method: 'post',
                    param: {
                        customerGuid: self.row.customerGuid,
                        customerState: self.normal ? 0 : 1
                    },
                    success: function (data) {
                        self.normal = !self.normal;
                        self.$refs.alerts.show({
                            msg: '设置成功'
                        });
                    },
                    error: function () {
                        self.$refs.alerts.show({
                            msg: data.message
                        });
                    }
                }, true)

            },
            cancelDialog: function () {
                this.$refs.cjqDetail.close();
                this.cjqDetail = false;
            },
            getDetails: function (row) {
                let current = this;
                if (current.index === 0) {
                    this.openDialogs(row);
                }
            },
            getParams: function () {
                let self = this;
                let arr = this.getSearchData(self[self.searchArr[self.index]]);
                this.searchTable(arr, 1);
            },
            getFrom: function (val) {
                if(val.combine){
                    if (this.useParam.deviceNo !== val.val) {
                        this.getOption(val, true);
                        this.getParams();
                    }
                }else {
                    let key = val.key.split(".");
                    if(key[0] === 'searchDate'){
                        let arr = val.val.split('至');
                        this[key[1]].startDate = arr[0];
                        this[key[1]].endDate = arr[1];
                        this.getParams();
                    }else{
                        if ( this[key[0]][val.key.split('.')[1]] !== val.val) {
                            this.getOption(val, true);
                            this.getParams();
                        }
                    }

                }
            },
            //点击tab标签页生成对应的table数据
            getTab: function (tab, index) {
                let self = this;
                self.index = index;
                this.$refs.myTable.resetTableOpt({column: self.columns[index], url: tab.url, exportURL: tab.exportURL, queryParam: self[self.searchArr[index]]});
            },
            getDetail: function () {
                let self = this;
                this.getRequest({
                    url: 'charger/customerInfoDetail',
                    method: 'post',
                    param: {
                        customerGuid: self.row.customerGuid,
                    },
                    success: function (data) {
                        self.Finish = true;
                        self.detailData = data.data;
                        self.normal = data.data.customerState == '0' ? false : true;
                    },
                    error: function () {

                    }
                }, true)
            },
            // 以下为增加弹框
            //电压折线图
            getLineC: function () {
                this.lineV  = echarts.init(this.$refs.currentChart);
                this.getLine(this.lineV, ['rgba(22,90,162, 1)', 'rgba(22,90,162, 0.1)'], ['电压', 'V'], 'yVoltageData', 'popup_blue')
            },
            //电流折线图
            getLineV: function () {
                this.lineA  = echarts.init(this.$refs.voltageChart);
                this.getLine(this.lineA, ['rgba(192,64,89, 1)', 'rgba(192,64,89, 0.1)'], ['电流', 'A'], 'yCurrentData', 'popup_red')
            },
            //功率折线图
            getLineR: function () {
                this.lineR = echarts.init(this.$refs.powerChart);
                this.getLine(this.lineR,  ['rgba(184,193,62, 1)', 'rgba(184,193,62, 0.1)'], ['功率', 'W'], 'yPowerData', 'popup_yellow')
            },
            getLine: function (obj, color, typeText, key, img) {
                let option = {
                    toolbox: {
                        show: false,
                        feature: {
                            dataZoom: {
                                yAxisIndex: 'none'
                            },
                            restore: {},
                            saveAsImage: {}
                        }
                    },
                    dataZoom: [
                        {
                            type: 'slider',
                            show: true,
                            xAxisIndex: [0],
                            start: 1,
                            end: 100
                        },
                        {
                            type: 'inside',
                            xAxisIndex: [0],
                            start: 0,
                            end: 100
                        }
                    ],
                    grid: {
                        left: 40,
                        right: 30,
                        bottom:40,
                        top: 50
                    },
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: ['1月' ,'2月', '3月', '4月', '5月', '6月', '7月','8月', '9月', '10月','11月','12月'],
                        nameTextStyle: {
                            color: 'rgba(255,255,255, 0.1)',
                        },
                        axisLabel: {
                            color: 'rgba(255,255,255, 0.1)',
                            textStyle: {
                                color:'rgba(255,255,255, 1)'
                            }
                        },
                        splitLine: {
                            show:true,
                            lineStyle: {
                                color: 'rgba(255,255,255, 0.1)',
                                type: 'dotted',
                                width: 2
                            }
                        },
                        axisLine: {
                            show:true,
                            lineStyle: {
                                color: 'rgba(255,255,255, 0.1)'

                            }
                        }
                    },
                    yAxis: {
                        name: typeText[0] + ' ( ' + typeText[1] + ' ) ',
                        type: 'value',
                        nameTextStyle: {
                            color: 'rgba(255,255,255, 1)',
                        },
                        axisLabel: {
                            color: 'rgba(255,255,255, 0.1)',
                            textStyle: {
                                color: 'rgba(255,255,255, 1)'
                            }
                        },
                        axisLine: {
                            show:true,
                            lineStyle: {
                                color: 'rgba(255,255,255, 0.1)'
                            }
                        },
                        splitLine: {
                            show:true,
                            lineStyle: {
                                color: 'rgba(255,255,255, 0.1)',
                                width: 2,
                                type: 'dotted'
                            }
                        }
                    },
                    tooltip : {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross'
                        }
                    },
                    series:
                        {
                            name: typeText[0],
                            type:'line',
                            smooth:false,
                            symbol: "circle",
                            symbolSize: 8,
                            // sampling: 'max',
                            itemStyle: {
                                normal: {
                                    color: color[0]
                                }
                            },
                            markPoint: {
                                // symbol: "pin",
                                data: [
                                    {
                                        type: 'max',
                                        name: '最大值',
                                        symbol: 'image://dist/img/widsomApp/'+img+'.png',
                                        symbolOffset: [0, '-55%'],
                                        label: {
                                            normal: {
                                                padding: [5,10,15,10],
                                                color: '#fff',
                                                position: 'inside'
                                            }

                                        }
                                    }
                                ]
                            },
                            areaStyle: {
                                normal: {
                                    color:  new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                        offset: 0,
                                        color: color[0]
                                    }, {
                                        offset: 1,
                                        color: color[1]
                                    }])
                                }
                            },
                            data: []
                        }
                };
                option.xAxis.data  = this.lineData.xData;
                option.series.data  = this.lineData[key];
                obj.clear();
                obj.setOption(option, true);
            },
            getLineData: function (row) {
                let that = this;
                that.getRequest({
                    url: 'charger/queryCurveCount',
                    method: 'post',
                    param: {
                        chargingPlieGuid: row.chargingPlieGuid,
                        chargingGuid: row.chargingGuid
                    },
                    success: function (data) {
                        let Data = data.data;
                        that.lineData.xData = Data.xData || [];
                        that.lineData.yPowerData = Data.yPowerData || [];
                        that.lineData.yCurrentData = Data.yCurrentData || [];
                        that.lineData.yVoltageData = Data.yVoltageData || [];
                        that.getLineC();
                        that.getLineV();
                        that.getLineR();
                    },
                    error: function () {
                        that.isLoading = true;
                    }
                }, true)
            },
            openDialogs: function (row) {
                let that = this;
                this.title = '设备编号:' + row.val.deviceNo + '，停充方式:' + row.val.deviceState;
                this.$refs.detail.show({
                    hasClose: true,
                    action:{
                        cancel: function () {
                            that.isDialogTable = false;
                            that.isLoading = false;
                        }
                    }
                });
                this.dialogTable.queryParam.chargingPlieGuid = row.val.chargingPlieGuid;
                this.dialogTable.queryParam.chargingGuid = row.val.chargingGuid;
                this.isDialogTable = true;
                this.getLineData(row.val);
            },
            windowChange: function () {
                 let self = this;
                 self.lineR !== ''?  self.lineR.resize() : '';
                 self.lineA !== ''?  self.lineA.resize() : '';
                 self.lineV !== ''?  self.lineV.resize() : '';
                 self.getHeight();
            },
            init: function () {
                this.getDetail();
                let dates = this.initDate(true);
                for (let i = 0; i < this.searchArr.length; i++){
                    this[this.searchArr[i]].customerGuid = this.row.customerGuid;
                    this[this.searchArr[i]].startDate = dates.start
                    this[this.searchArr[i]].endDate = dates.end;
                }
                this.tableOption.isExport = this.controls[6];
                this.dialogTable.isExport = this.controls[6];
                this.tableOption.column = this.columns[0];
                this.tableOption.queryParam = this.useParam;
                this.tableShow = true;
            },
            resetComponent: function () {
                let that = this;
                that.show = false;
                that.$emit('closeDiv');
                setTimeout(function () {
                    that.show = false;
                }, 0)
            }
        },
        mounted: function () {
            this.init();
            this.windowChange();
        }
    }
</script>
<style lang="scss">
    $headerColor:#44BBDD;
    $leftContentW: 16%;
    $leftContentH: 1em;
    .staffMana-info{
        .info-container{
            margin-top:5px
        }
        .list-operate{
            line-height: 1.3;
        }
        .content-left{
            width: $leftContentW;
            font-size: 0.875em;
            margin: 1.5em;
            padding: 0.6em 1.25em;
            min-height: $leftContentH;
            border: 0.2em solid #3E8BDB;
            border-radius: 0.4em;
            float:left;
            max-height: calc(100% - 4em);
            p.HF-p{
                font-size: 1em;
                text-align:left;
                margin:0;
                i.hasIcon{
                    float:right;
                    color: #3E8BDB;
                    margin-right: 0.5em;
                    cursor: pointer;
                }
                i.hasIcon:hover{
                    color: white
                }
            }
            p.content-p{
                margin: 2em;
                text-align: center;
                span{
                    font-size: 2.7em;
                    color: #EE8836;
                    margin-right:0.3em;
                    font-weight: 600;
                }
            }
        }
        .content-right{
            float:left;
            margin: 1em 1.5em 1em 2.5em;
            width: calc(84% - 10em);
            min-height: calc(#{$leftContentH} + 0.4em + 2.5em);
            border-bottom: 1px solid #435273;
            .form-list{
                li {
                    .label-show{
                        min-width:7em
                    }
                    .operate-div{
                        padding: 0;
                        margin-left: 2.5em
                    }
                }
            }
        }
        .content-bottom{
            float: left;
            margin-left: 3%;
            .list-operate{
                width: 5em;
                vertical-align: middle;
                position: relative;
                top: 2px;
            }
            .normal{
                background: url("/dist/img/widsomApp/normal.png") no-repeat;
                background-size: 100% 100%;
            }
            .defriend{
                background: url("/dist/img/widsomApp/defriend.png") no-repeat;
                background-size: 100% 100%;
            }
        }
        .staffMana-content{
            width: 100%;
            .mychar-box{
                width: 100%;
                height: 15em;
                display: flex;
                justify-content: space-around;
                &>div{
                    height: 15em;
                    width: 33%;
                }
            }
        }
    }
</style>



