<template>
    <div ref="trys" style="height: 100%">
        <div v-if="showInfo" class="info-container">
            <info :row="row" @closeDiv="closeDiv" :controls="controls" ref="infoDetail"></info>
        </div>
        <div  v-show="!showInfo"  class="all-container">
            <div class="operate-div">
                <template v-for="(item, $index) in formList">
                    <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom"></inputs>
                    <selects ref="selects"  v-if="item.type == 'select'" :option="item.options" @getSelect="getFrom"></selects>
                </template>
            </div>
            <div ref="myOwnTable" class="table-container" >
                <tables ref="myTable" :tableOpt="tableOpt" class="tablePadding" v-if="tableShow"
                        @rowDbClick="checkInfo"></tables>
            </div>
        </div>
        <dialogs type="alert" ref="alerts"></dialogs>
    </div>
</template>

<script>
    import info from './info.vue'
    export default{
        data() {
            return {
                showInfo: false,
                selected: [],
                searchParam: {
                    customerState:'',
                    customerContact: "",
                    webcharNo: '',
                    customerName: '',
                    alipayUserId: '',
                    alipayNickName: '',
                    endDate: '',
                    startDate: ''
                },
                row: '',
                controls: [],
                tableOpt: {
                    column: [
                        {title: '序号', field: 'Index',textAlign: 'center',width: '40'},
                        {title: '状态', field: 'customerState', textAlign: 'center', width: '90',type: 'string',
                            formatter: function (val, index, row, obj) {
                                let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                                switch(vals) {
                                    case '运行中':
                                        return `<div class="green">运行中</div>`
                                    case '黑名单':
                                        return `<div class="red">黑名单</div>`;
                                }

                            }},
                        {title: '手机号', field: 'customerContact', textAlign: 'center', width: '90'},
                        {title: '账户余额(元)', field: 'remainAmount', textAlign: 'center', width: '100',type: 'string',
                            formatter: function (val, index, row, obj) {
                                return `<div style="color:#EC7E27">¥ ${val}</div>`

                            }},
                        {title: '月卡剩余次数', field: 'remainCnt', textAlign: 'center', width: '150'},
                        {title: '充电次数', field: 'chargeCount', textAlign: 'center', width: '130'},
                        {title: '支付宝(昵称)', field: 'alipayNickName', textAlign: 'center', width: '120'},
                        {title: '微信(昵称)', field: 'customerName', textAlign: 'center', width: '120'},
                        {title: 'IC卡', field: 'cardId', textAlign: 'center', width: '100'},
                        {title: '更新时间', field: 'updateTime', textAlign: 'center', width: '120'}
                    ],
                    // method: 'get',
                    // url: 'testJson/Main.json',
                    url: "charger/customerInfoList",
                    method: 'post',
                    pageSize: 20,
                    pageNumber: 1,
                    pageList: [20, 40, 60],
                    queryParam: {},
                    isExport: true,
                    exportURL: 'charger/customerInfoListDownload'
                },
                tableShow: false,
            }
        },
        components: {
            info
        },
        computed: {
            formList: function () {
                return [
                    {
                        type: 'select',
                        options: {
                            width: '6em',
                            cs: 'client',
                            data: [
                                {text: '全部状态', value: 2},
                                {text: '黑名单', value: 0},
                                {text: '运行中', value: 1},
                            ],
                            key: 'searchParam.customerState'
                        }
                    },
                    {
                        type: 'input',
                        options: {
                            type: 'typeSearch',
                            key: 'searchParam',
                            selectOpt: {
                                width: '8em',
                                cs: 'client',
                                data: [
                                    {text: '手机号', value: 'customerContact'},
                                    {text: '微信号', value: 'webcharNo'},
                                    {text: '微信昵称', value: 'customerName'},
                                    {text: '支付宝号', value: 'alipayUserId'},
                                    {text: '支付宝昵称', value: 'alipayNickName'},
                                ],
                                text: 'text',
                                value: 'value',
                                key: 'searchType'
                            }
                        }
                    }
                ]
            },
        },

        methods: {
            checkInfo: function (val) {
                this.row = val.val;
                this.showInfo = true;
            },
            closeDiv: function () {
                this.showInfo = false;
                this.getParams();
            },
            getFrom: function (val) {
                if (val.combine) {
                    if ( this.searchParam[val.key.split('.')[1]] !== val.val) {
                        this.searchParam = this.cleanSearchData(this.searchParam, ['customerState']);
                        this.getOption(val, true);
                        this.getParams(1);
                    }
                } else {
                    this.getOption(val, true);
                    this.getParams(1);
                }
            },
            getParams: function (pageNumber) {
                let arr = this.getSearchData(this.searchParam);
                this.getHeight();
                this.searchTable(arr, pageNumber);
            },
            windowChange: function () {
                let self = this;
                self.getHeight();
                // window.onresize = function () {
                //     self.getHeight();
                // }
            },
            init: function () {
                let self = this;
                this.tableOpt.isExport = this.controls[6];
                this.tableOpt.queryParam = this.getSearchData(this.searchParam);
                this.tableShow = true;
                // window.onresize = function () {
                //     self.windowChange()
                // }
            },
        },
        created: function () {
            this.isIn();
            this.getAction();
        },
    }
</script>

<style lang="scss">
    .dialog-ul .hasCheck-allWidth {
        width: fit-content!important;
        width:  -webkit-fit-content!important;
        width:  -moz-fit-content!important;
        width:max-content;
        float: left;
        .inputChoose{
            line-height: 2.5;
            padding-left: 1em;
        }
        input.checkBox{
            left:0.7em
        }
    }
</style>


